import React, {Component} from 'react';
import './App.scss';
import Display from './Display';
import close from './images/close.png';
import open from './images/open.png';
import ClickNHold from 'react-click-n-hold';
import open2 from './images/open2.png';
import close2 from './images/close2.png';
import batteryImage from './images/battery.png';
import * as classnames from 'classnames';

String.prototype.addAt = function(index, add) {
	return this.substr(0, index + 1) + add + this.substr(index + add.length);
};
const unitsToDecimals = {
	'units': 1,
	'g': 2,
	'oz': 3,
};
const UNIT = 'units';
const GR_IN_OZ = 0.035274;
const DEFAULT_UNIT_AMOUNT = 0.05;
const TIMEOUT = 15000;

const getAmountByUnit = (isUnit, amount, unitAmount, units) => {
	let resolvedAmount = units === 'oz' ? amount * GR_IN_OZ : amount;
	resolvedAmount = isUnit ? (amount / unitAmount) : resolvedAmount;
	return Math.abs(Math.round(100 * resolvedAmount) / 100);
};
const padLeft = resolve => {
	if (resolve < 10) {
		return `000${resolve}`;
	} else if (resolve < 100) {
		return `00${resolve}`;
	} else if (resolve < 1000) {
		return `0${resolve}`;
	}
	return resolve;
};
const padWithZeros = (amount, decimals) => {
	const number = round(amount, decimals);
	const leftDigits = 4 - decimals;
	let resolve = number * Math.pow(10, decimals);
	resolve = String(padLeft(resolve > 9999 ? 9999 : resolve));
	return resolve.addAt(leftDigits - 1, '.');
};

class App extends Component {

	constructor(props) {
		super(props);
		this.state = {
			isBattery: false,
			hadChange: false,
			showAll: true,
			isLive: false,
			isCoverOpen: false,
			isUnit: false,
			totalAmount: 0,
			inputAmount: '',
			currentAmount: 0,
			unitAmount: DEFAULT_UNIT_AMOUNT,
			units: 'g',
		};
	}

	timer = () => setTimeout(() => { // return the timeoutID
		console.log('sleep');
		this.setState({
			isLive: false,
		});
	}, TIMEOUT);

	componentWillUpdate(nextProps, nextState) {
		if (this.timeoutID) {
			clearTimeout(this.timeoutID);
		}
		this.timeoutID = this.timer();
	}

	render() {
		const {isLive, isCoverOpen, showAll, hadChange, isBattery, totalAmount, units, isUnit, unitAmount, currentAmount, inputAmount} = this.state;
		const iterationMode = hadChange && !showAll;
		return (
				<div className="App">
					<div className="control-panel">
						<Control
								isCoverOpen={isCoverOpen}
								amount={inputAmount}
								onInputChange={this.onInputChange}
								onPlus={this.onWeighButtonClick(true)}
								onMinus={this.onWeighButtonClick(false)}
								onCover={this.onCoverButtonClick}
								onBattery={() => this.setState({isBattery: !isBattery})}
						/>
						<img width={isCoverOpen ? 280 : 180}
						     src={isCoverOpen ? open2 : close2}
						     alt={String(isCoverOpen)}/>
						<Data live={isLive}
						      cover={isCoverOpen}
						      unit={unitAmount}
						      current={round(currentAmount, 4)}
						      total={round(totalAmount, 4)}
						/>
					</div>
					<LongPressButton
							className={'power-button'}
							onClick={this.onPowerButtonClick}
							onLongClick={this.onPowerButtonLongClick}
							inverted={isCoverOpen}
					/>
					<LongPressButton
							className={'display-button'}
							inverted={isCoverOpen}
							onClick={this.onDisplayClick}
							onLongClick={this.onDisplayLongClick}
					/>
					<Display
							battery={isBattery}
							isFlipped={isCoverOpen}
							isLive={isLive}
							amount={this.resolveDisplayAmount()}
							units={isUnit ? UNIT : units}
							sign={currentAmount !== 0 && iterationMode ? (currentAmount > 0
									? '+'
									: '-') : ''}
							total={showAll}
					/>
					<img className={'cover'} src={isCoverOpen ? open : close} alt=""/>
				</div>
		);
	}

	resolveDisplayAmount = () => {
		const {totalAmount, isUnit, unitAmount, currentAmount, showAll, hadChange, units} = this.state;
		const iterationMode = hadChange && !showAll;
		const amount = iterationMode ? currentAmount : totalAmount;
		return padWithZeros(getAmountByUnit(isUnit, amount, unitAmount, units),
				unitsToDecimals[isUnit ? UNIT : units]);
	};
	onInputChange = event => this.setState(
			{inputAmount: Number(event.target.value)});

	onWeighButtonClick = add => () => {
		const {hadChange, isCoverOpen, totalAmount, currentAmount, inputAmount} = this.state;
		!hadChange && this.setState({
			currentAmount: 0,
			hadChange: true,
		});
		if (isCoverOpen) {
			this.setState({
				totalAmount: add ? (totalAmount + inputAmount) : (totalAmount - inputAmount),
				currentAmount: add ? (currentAmount + inputAmount) : (currentAmount - inputAmount),
				isLive: true,
			});
		} else {
			window.alert('Please open cover first');
		}
	};
	onDisplayClick = () => {
		const {isUnit} = this.state;
		this.setState({isUnit: !isUnit});
	};
	onDisplayLongClick = () => {
		const {isUnit, units, totalAmount} = this.state;
		return isUnit
				? this.setState(
						{unitAmount: totalAmount ? totalAmount : DEFAULT_UNIT_AMOUNT})
				: this.setState({units: units === 'oz' ? 'g' : 'oz'});
	};
	onPowerButtonLongClick = () => this.state.isCoverOpen &&
			this.setState({currentAmount: 0});

	onPowerButtonClick = () => {
		const {isLive, isCoverOpen, showAll} = this.state;
		if (isLive) {
			return isCoverOpen ? this.setState({showAll: !showAll}) : this.turnOff();
		} else {
			this.setState({isLive: true});
		}
	};
	onCoverButtonClick = () => {
		const {isLive, isCoverOpen} = this.state;
		this.setState({
			isCoverOpen: !isCoverOpen,
			showAll: isCoverOpen,
		});
		isCoverOpen && this.setState({currentAmount: 0, hadChange: false});
		!isLive && !isCoverOpen && this.setState({isLive: true});
	};
	turnOff = () => this.setState(
			{isLive: false, hadChange: false, inputAmount: '', currentAmount: 0});
}

export default App;

const LongPressButton = ({onClick, onLongClick, className, inverted}) => (
		<ClickNHold
				time={1.7}
				onEnd={(e, enough) => enough ? onLongClick() : onClick()}>
			<div className={classnames(
					{'--inverted': inverted, '--regular': !inverted}, className)}/>
		</ClickNHold>);

const Data = ({live, cover, unit, total, current}) => (
		<div className="data">
			<KeyValuePair obj={{key: 'Status', value: live ? 'On' : 'Off'}}/>
			<KeyValuePair obj={{key: 'Cover', value: cover ? 'Open' : 'Close'}}/>
			<KeyValuePair obj={{key: 'Unit', value: unit}}/>
			<KeyValuePair obj={{key: 'Total Amount (gr)', value: total}}/>
			<KeyValuePair obj={{key: 'Current Amount (gr)', value: current}}/>
		</div>
);

const KeyValuePair = ({obj}) => (
		<div>
			<span className="key">{obj.key}:</span> {obj.value}
		</div>
);
const Control = ({onInputChange, amount, onPlus, onMinus, onCover, onBattery, isCoverOpen}) => (
		<div className='control'>
			<div className={'weight-control'}>
				<input onChange={onInputChange}
				       disabled={!isCoverOpen}
				       type="number"
				       className="weight-input"
				       value={amount ? amount : ''}/>
				<div className='plus-minus'>
					<Button
							className={'control-button'}
							onClick={onPlus}
							label='+'/>
					<Button
							className={'control-button'}
							onClick={onMinus}
							label='-'/>
				</div>
			</div>
			<div className='control-second-row'>
				<Button
						className={'cover-button'}
						onClick={onCover}
						label={isCoverOpen ? 'Close Cover' : 'Open Cover'}
				/>
				<div className={'battery-switch'}>
					<img className={'battery'} src={batteryImage}/>
					<input type='checkbox' onChange={onBattery}
							// className={'regular-button'}
							   onClick={onBattery}
							// label='Battery'
					/>
				</div>
			</div>
		</div>
);

const Button = ({onClick, className, label}) => (
		<div className={className} onClick={onClick}>{label}</div>);

