import React, {Component, Fragment} from 'react';
import g from './images/g.png';
import unit from './images/unit.png';
import oz from './images/oz.png';
import batteryImg from './images/battery.png';
import classnames from 'classnames';

const Display = ({isLive, amount, units, isFlipped, sign, total, battery}) => (
		<div className={classnames({
			'Display': true,
			'flipped': isFlipped,
			'On': isLive,
			'Off': !isLive,
		})}>
			{isLive && <div className='screen'>
				<Unit unit='oz' isSelected={units === 'oz'}/>
				<Unit unit='g' isSelected={units === 'g'}/>
				<Unit unit='units' isSelected={units === 'units'}/>
				<Amount amount={amount}/>
				<div className="sign">{sign}</div>
				{battery && <img className={"battery"} src={batteryImg} alt={'battery'}/>}
				<div className={classnames({'total': true, 'invisible': !total })}>Total
				</div>
			</div>}
		</div>
);
const Amount = ({amount}) => (
		<div className="display-amount">
			{amount.split('').map((char, i) =>
					<div key={`digit${i}`}
					     className={char !== '.' && 'amount-digit'}>{char}</div>)}
		</div>
);
const getUnitsImage = units => units === 'g' ? g :
		(units === 'oz' ? oz : unit);
export default Display;
const Unit = ({unit, isSelected}) => <img
		className={classnames({[unit]: true, On: isSelected, Off: !isSelected})}
		src={getUnitsImage(unit)} alt={unit}/>;
